const Icon = (props) => {

    return <div style={{ display: "flex", alignItems: "center" }}>
        <img
            src={`${process.env.PUBLIC_URL}/assets/icons/${props.name}.svg`}
            style={{ filter: `invert(${props.color})` }}
            width={props.width}
            height={props.height}
            alt={`${props.name}`}
        />
        {props.text && <span style={{ marginLeft: 10 }}>{props.text}</span>}

        {props.children && <div style={{ marginLeft: 10 }}>
            {props.children}
        </div>}

    </div>
}

Icon.defaultProps = {
    width: 30,
    height: 30,
    text: "",
    color: 1
}

export default Icon;