const accessToken = "IGQVJVNE92YWRwMDc5RUo5bW5MZAWF2QjlkSTZAmZAk52MXFsNzM1UmwtV2hqUGtnOGlhc0dFYnZAnZAUtYSE5yNWJwTmJYMjNETFI2aWthWmswcnoxWVMybnF4X0FnSWNMNDcxVTQ4eklHc1dFTmhmbFdWOAZDZD";


const InstagramBlock = (props) => {

    const media = `https://graph.instagram.com/me/media?fields=id,caption,media_url,timestamp,username,thumbnail_url,permalink,media_type&access_token=${accessToken}`;

    const [posts, setPosts] = useState([]);
    useEffect(() => {
        axios.get(media)
            .then(response => {
                const { data } = response;
                setPosts(data.data.slice(0, 6));
                console.log(data.data);
            })
            .catch(error => {
                if (error.response) {
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.headers);
                } else if (error.request) {
                    console.log(error.request);
                } else {
                    console.log('Error', error.message);
                }
            });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return <>
        <div className="insta">
            {posts.map((it, key) => {
                return <Card className="insta-item" key={key}>
                    <img src={it.media_url} alt="asdf" />
                    <div dangerouslySetInnerHTML={{ __html: it.caption.replace("\r", "").replace("\n", "<br /><br />") }}
                    />
                </Card>
            })}
        </div>
    </>
}

export default InstagramBlock;
