import { useState } from 'react';
import { Link } from "react-router-dom";
import { HashLink } from 'react-router-hash-link';

const Nav = (props) => {

    const [toggle, setToggle] = useState(false);

    const generateLink = (id, title) => {
        return <li
            className={`item ${toggle ? "active" : ""}`}
            onClick={() => setToggle(false)}
        >
            <HashLink to={`/#${id}`}>{title}</HashLink>
        </li>
    }

    return (
        <nav>
            <div style={{ display: "flex", justifyContent: "center" }}>
                <div style={{ width: 900 }}>
                    <ul className="menu">
                        <li className="logo" onClick={() => setToggle(false)}>
                            <Link to="/">El Búhu Tuerto</Link>
                        </li>
                        { generateLink("news", "News") }
                        { generateLink("restaurant", "Restaurant")}
                        { generateLink("about", "About")}      
                        <li
                            className={`item button ${toggle ? "active" : ""}`}
                            onClick={() => setToggle(false)}
                        >
                            <Link to="/menu">Menu</Link>
                        </li>
                        <li className="toggle" onClick={(e) => {
                            setToggle(!toggle);
                        }}>
                            <span className="bars"></span>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>)
}

export default Nav;
